
 /* Contenedor principal */

.contenedor {
    background-image: url("../img/fondo01.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
}

.contenedorTitulo {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.titulo,
.subTitulo, .btnStart{
    color: #DFF9F0;
    margin-right: 100px;
    transition: color .9s;
}

.subTitulo {
    margin-top: 50px;
}

.btnStart{
    background-color: #16AD80;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    margin-top: 50px;
    margin-right:150px;
    font-size: 30px;
    border-radius: 50% 50% 50% 50% / 50% 50% 50% 50%;
    text-align: center; 
    box-shadow: 0px 0px 20px 6px #fff;
}
.btn{
    text-decoration: none;
    color: #fff;
    transition: all .6s;
}
.btn:hover{
    transform: scale(1.2);
    color: #F96308;
}